"use client";
import { useState, useRef, useCallback, useEffect } from 'react';

const SYSTEM_INSTRUCTION = "أنت المشرف الإلكتروني الميداني. الفني يرسل لك الآن صورة وتسجيلاً صوتياً. حلل الصورة بدقة وأجب على سؤاله صوتياً باختصار شديد باللغة العربية.";

export function useGeminiLive() {
    const [status, setStatus] = useState<"Offline" | "Camera Ready" | "Recording..." | "Thinking..." | "AI is speaking">("Offline");
    const videoRef = useRef<HTMLVideoElement | null>(null);
    const streamRef = useRef<MediaStream | null>(null);
    const mediaRecorderRef = useRef<MediaRecorder | null>(null);
    const audioChunksRef = useRef<Blob[]>([]);

    const connect = useCallback(async () => {
        setStatus("Connecting...");
        try {
            const stream = await navigator.mediaDevices.getUserMedia({
                video: { facingMode: "environment" },
                audio: true
            });
            streamRef.current = stream;
            if (videoRef.current) videoRef.current.srcObject = stream;
            setStatus("Camera Ready");
        } catch (error) {
            setStatus("Offline");
        }
    }, []);

    const disconnect = useCallback(() => {
        setStatus("Offline");
        if (streamRef.current) streamRef.current.getTracks().forEach(track => track.stop());
        if (videoRef.current) videoRef.current.srcObject = null;
    }, []);

    const askSupervisor = async () => {
        if (!streamRef.current || status === "Thinking...") return;

        const apiKey = process.env.NEXT_PUBLIC_GEMINI_API_KEY;

        // 1. التقاط صورة
        const canvas = document.createElement("canvas");
        const ctx = canvas.getContext("2d");
        let base64Image = "";
        if (videoRef.current && ctx) {
            canvas.width = videoRef.current.videoWidth;
            canvas.height = videoRef.current.videoHeight;
            ctx.drawImage(videoRef.current, 0, 0);
            base64Image = canvas.toDataURL("image/jpeg", 0.6).split(",")[1];
        }

        // 2. تسجيل صوتي سريع (3 ثوانٍ)
        setStatus("Recording...");
        audioChunksRef.current = [];
        const mediaRecorder = new MediaRecorder(streamRef.current);
        mediaRecorder.ondataavailable = (e) => audioChunksRef.current.push(e.data);
        mediaRecorder.onstop = async () => {
            setStatus("Thinking...");
            const audioBlob = new Blob(audioChunksRef.current, { type: 'audio/webm' });
            const reader = new FileReader();
            reader.readAsDataURL(audioBlob);
            reader.onloadend = async () => {
                const base64Audio = (reader.result as string).split(",")[1];
                await sendRequest(apiKey!, base64Image, base64Audio);
            };
        };
        mediaRecorder.start();
        setTimeout(() => mediaRecorder.stop(), 3000);
    };

    const sendRequest = async (apiKey: string, img: string, aud: string) => {
        const url = `https://generativelanguage.googleapis.com/v1beta/models/gemini-2.0-flash:generateContent?key=${apiKey}`;
        try {
            const res = await fetch(url, {
                method: "POST",
                body: JSON.stringify({
                    system_instruction: { parts: [{ text: SYSTEM_INSTRUCTION }] },
                    contents: [{
                        parts: [
                            { text: "أجب صوتياً على هذا السؤال بناءً على الصورة." },
                            { inline_data: { mime_type: "image/jpeg", data: img } },
                            { inline_data: { mime_type: "audio/webm", data: aud } }
                        ]
                    }]
                })
            });
            const data = await res.json();
            const text = data.candidates[0].content.parts[0].text;

            setStatus("AI is speaking");
            const speech = new SpeechSynthesisUtterance(text);
            speech.lang = 'ar-SA';
            speech.onend = () => setStatus("Camera Ready");
            window.speechSynthesis.speak(speech);
        } catch (e) {
            setStatus("Camera Ready");
        }
    };

    return { status, connect, disconnect, videoRef, askSupervisor };
}