"use client";

import { useGeminiLive } from "@/hooks/useGeminiLive";
import { Play, Square } from "lucide-react";

export default function Home() {
    const { status, connect, disconnect, videoRef } = useGeminiLive();

    return (
        <main className="relative h-screen w-full bg-black overflow-hidden flex flex-col items-center">
            {/* Camera Feed */}
            <video
                ref={videoRef}
                autoPlay
                playsInline
                muted
                className="absolute inset-0 w-full h-full object-cover"
            />

            {/* Status Overlay */}
            <div className="absolute top-8 left-1/2 -translate-x-1/2 z-10 px-6 py-2 rounded-full bg-black/60 backdrop-blur-md border border-white/10 flex items-center gap-2">
                <div className={`w-3 h-3 rounded-full ${status === 'AI is watching' ? 'bg-green-500 animate-pulse' : status === 'Connecting...' ? 'bg-yellow-500 animate-pulse' : 'bg-red-500'}`} />
                <span className="text-white text-sm font-medium tracking-wide">
                    {status}
                </span>
            </div>

            {/* Controls */}
            <div className="absolute bottom-12 w-full px-6 flex justify-center z-10">
                {status === "Offline" ? (
                    <button
                        onClick={connect}
                        className="w-full max-w-sm flex items-center justify-center gap-3 bg-green-500 hover:bg-green-600 active:bg-green-700 text-white font-bold py-5 rounded-3xl shadow-lg transition-transform active:scale-95"
                    >
                        <Play className="w-8 h-8" />
                        <span className="text-xl">Connect to AI Supervisor</span>
                    </button>
                ) : (
                    <button
                        onClick={disconnect}
                        className="w-full max-w-sm flex items-center justify-center gap-3 bg-red-500 hover:bg-red-600 active:bg-red-700 text-white font-bold py-5 rounded-3xl shadow-lg transition-transform active:scale-95"
                    >
                        <Square className="w-8 h-8" fill="currentColor" />
                        <span className="text-xl">Disconnect</span>
                    </button>
                )}
            </div>
        </main>
    );
}
